<template>
  <v-navigation-drawer
    id="menu-lateral"
    color="primaryDark"
    class="fullDrawer open"
    v-model="drawer"
    absolute
    temporary
    app
  >
    <v-list dense>
      <template v-for="item in items">
        <v-list-item
          class="btn-lateral my-1"
          :class="isActive(item.alias)"
          :key="item.text"
          @click="goTo(item.routeName)"
          rounded
          dark
        >
          <v-list-item-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>
              {{ item.text }}
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </template>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
// Mixins
import NavigationMixin from '@/mixins/NavigationMixin'

export default {
  name: 'NavigationDrawer',
  mixins: [ NavigationMixin ],
  props: {
    alias: {
      default: ''
    }
  },
  data: () => ({
    drawer: false,
    items: [
      { icon: 'calendar_today', text: 'Meus Agendamentos', routeName: 'admin-schedule', alias: 'schedule' },
      { icon: 'favorite', text: 'Favoritos', routeName: 'admin-service', alias: 'service' },
      { icon: 'star', text: 'Avaliações', routeName: 'admin-barbershop', alias: 'dashboard' },
    ]
  }),
  methods: {
    isActive (itemAlias) {
      return this.alias === itemAlias ? 'active' : ''
    },
    toggleNavigationDrawer () {
      this.drawer = !this.drawer
    }
  }
}
</script>